* {
  font-family: "Roboto", sans-serif;
  box-sizing: border-box;
}

body {
  margin: 0;
}

a {
  text-decoration: none;
  color: #377e9a;
}

.container {
  max-width: 1150px;
  margin: auto;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.flexcard {
  width: 22rem;
  display: flex;
  flex-direction: row;
  margin-right: 10px;
  margin-left: 5px;
  margin-bottom: 20px;
}

.main {
  background-color: #f5f5f5;
  /* background-image: url(./assets/main_bg.png); */
  background-size: cover;
  background-repeat: no-repeat;
}

.pages {
  /* padding: 15px 0; */
  position: relative;
  flex: 1;
}

.page {
  border-radius: 16px;
  background: white;
  transition: visibility 0.3s, opacity 0.3s ease-in-out;
  box-shadow: 0px 15px 25px 0px rgba(0, 0, 0, 0.1);
}

.page__header {
  padding: 100px 30px;
  /* background-color: #eb8a23; */
  color: white;
  background-image: url(./assets/img/home.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
  display: flex;
  align-items: center;
}

.img__mask {
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.4);
}

.page__title {
  margin: 0;
  text-align: center;
  font-size: 44px;
}

.page__content {
  padding: 32px 64px;
  background-color: white;
  border-bottom-left-radius: 16px;
  border-bottom-right-radius: 16px;
}

.homepage__header {
  display: flex;
  padding-top: 100px;
  align-items: center;
}

.homepage__title {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

