/* ===== Signup css */
.signup-title {
  font-size: 30px;
  color: #000;
  font-weight: bold;
  margin-top: 40px;
  margin-bottom: 25px;
  text-align: center;
}

.signup-section-wrapper {
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  flex-direction: column;
  padding: 20px 100px;
  background-color: #fff;
}
@media (max-width: 991px) {
  .signup-section-wrapper {
    padding-left: 50px;
    padding-right: 50px;
  }
}
@media (max-width: 575px) {
  .signup-section-wrapper {
    padding-top: 20px;
    padding-bottom: 20px;
    min-height: 100vh;
  }
}

.signup-btn {
  width: 40%;
  padding: 13px 20px;
  background-color: #eb8a23;
  border-radius: 0;
  font-size: 20px;
  font-weight: bold;
  color: #fff;
  margin-left: 30%;
  margin-bottom: 14px;
  display: flex;
  justify-content: center;
}
.signup-btn:hover {
  border: 1px solid #eb8a23;
  background-color: #fff;
  color: #eb8a23;
}

.signup-wrapper-footer-text {
  font-size: 16px;
  color: #000;
  /* padding-left: 200px; */
  text-align: center;
  margin-top: 15px;
  display: flex;
  justify-content: center;
}

.signup-wrapper-footer-text .switch-login:hover {
  text-decoration: underline;
  color: #eb8a23;
}

